<template>
  <div class="modal-document">
    <h4 class="fw-bold">Przechodniu powiedz Polsce…</h4>
    <p class="fw-bold">
      Raymond Graham Swing, niezwykle popularny w Stanach Zjednoczonych spiker radiowy, w audycji popołudniowej 18 maja
      1944 roku ogłosił na całą Amerykę: „Wspaniały manewr oskrzydlający, który spowodował zdobycie Cassino, udał się
      dzięki natarciu wojsk polskich, najwspanialszych żołnierzy, jakich wydała obecna wojna”.
    </p>
    <p>
      Bezsprzecznie dzień zatknięcia na ruinach benedyktyńskiego opactwa na Monte Cassino biało-czerwonej flagi był dla
      polskich żołnierzy wielkim triumfem. Zostali wyróżnieni spośród wszystkich armii walczących o tę wydawałoby się
      nie do zdobycia niemiecką redutę, zagradzającą aliantom drogę do Rzymu. I rzeczywiście, od początku 1944 roku po
      kolei wykrwawiały się tutaj najlepsze wojska sprzymierzonych. W pierwszym, styczniowym natarciu, w bagnach rzeki
      Rapido zginęło ponad 2000 żołnierzy amerykańskich i francuskich. Drugie natarcie w połowie lutego rozpoczęli
      Anglicy. Kiedy wykrwawili się na wzgórzu 593, zastąpili ich Hindusi i równie mocno zostali zdziesiątkowani. Wprost
      na dolinę Liri uderzyli Nowozelandczycy, w tym słynni z bitności Maorysi – i ich atak zakończył się
      niepowodzeniem. Po ciężkich walkach pod Monte Cassino korpus nowozelandzki został rozwiązany…
      W trzecim, marcowym natarciu, miasteczko Cassino zmienione zostało w gruzowisko, a od ruin klasztoru odrzuceni
      zostali waleczni Gurkhowie – to była rzecz niebywała! Jak pisał Melchior Wańkowicz, sławna hinduska 4 Dywizja
      Piechoty, która w tym ataku straciła 3000 żołnierzy, pierwszy raz w swej historii nie osiągnęła celu ataku.
      Niemieccy strzelcy spadochronowi w pełni potwierdzali, że zasługują na miano wojskowej elity Hitlera. Alianci
      nazwali ich Zielonymi Diabłami.
      Zniecierpliwiony i zdenerwowany Winston Churchill pisał do generała Harolda Alexandra, dowódcy 15 Grupy Armii we
      Włoszech: „Chciałbym, żeby mi pan wyjaśnił, dlaczego przejście pod Cassino, Górę Klasztorną etc. szerokości
      zaledwie dwu lub trzech mil jest jedynym miejscem, w które musi pan uderzać. Pięć czy sześć dywizji zużyło się już
      podczas włażenia w tę paszczę… Bardzo trudno zrozumieć, dlaczego najsilniej bronione miejsce ma być jedyną drogą
      do przodu…”. Alexander odpowiedział w długiej depeszy, możliwie jak najdokładniej wyjaśniając, że nie ma innej
      drogi na Rzym. Podkreślił przy tym bardzo ciężkie warunki terenowe i atmosferyczne, które od jesieni ubiegłego
      roku gnębiły aliantów. Wspomniał wreszcie, że jego wojska muszą zmagać się z doborowymi oddziałami
      nieprzyjacielskimi. Premier odpowiedział mu krótko i sarkastycznie: „Bardzo dziękuję za tak pełne wyjaśnienie… Z
      pewnością nieprzyjacielowi też jest bardzo ciężko”.

    </p>
    <h4 class="fw-bold">2 Korpus wchodzi do akcji</h4>
    <p>
      Po fiasku trzeciego alianckiego natarcia na Monte Cassino w drugiej połowie marca 1944 roku na froncie nastąpiła
      stagnacja. Dla sprzymierzonych stało się jasne, że do przełamania linii Gustawa potrzebne są świeże posiłki.
      Wybiła godzina dla 2 Korpusu Polskiego generała Władysława Andersa, który na ziemi włoskiej znalazł się w grudniu
      1943 roku i wszedł w skład brytyjskiej 8 Armii. Churchill pisał wówczas do generała Alana Brooke’a, szefa Sztabu
      Imperialnego: „Wejście Polaków do boju staje się obecnie kwestią pilną”.
      24 marca 1944 roku, generał Oliver Leese, dowódca 8 Armii, spotkał się w Vinchiaturo z generałem Andersem. Dowódca
      2 Korpusu zgodził się, by jego żołnierze wzięli udział w czwartym, wiosennym natarciu na linię Gustawa. Jak
      wspomina generał Anders: „Początkowo zadanie postawione 2-mu Korpusowi Polskiemu przez dowódcę 8-ej Armii było
      ujęte ogólnie: «zdobyć Monte Cassino i działać na Piedimonte»”. Co oznaczało ni mniej ni więcej, tylko przełamanie
      linii Gustawa w jej newralgicznym miejscu i uderzenie na najmocniejszą redutę w linii Hitlera. Generał Anders
      uszczegółowił ten plan: „Przewidywałem przede wszystkim przełamanie pozycji obronnej powyżej Monte Cassino,
      bardziej na północ, celem uzyskania naprzód całkowitego odosobnienia klasztoru, który miał być zdobywany dopiero w
      następnej fazie. Przedmiotem pierwszego natarcia były główne bastiony systemu obrony: wzgórze 593 i Colle San
      Angelo. Opanowanie Colle San Angelo miało spowodować pełne odosobnienie klasztoru”.
      Oprócz 2 Korpusu Polskiego do natarcia wyznaczono brytyjski 13 Korpus. Brytyjczycy mieli sforsować rzekę Rapido i
      uchwycić na niej przedmościa między Cassino a rzeką Liri, odizolować Cassino od zachodu i po otworzeniu drogi
      numer 6, uderzyć na linię Hitlera. Przewidywano, że jeśli obydwa korpusy wykonają zadanie, linia Gustawa w końcu
      pęknie. Pomóc im w tym mieli Marokańczycy i Tunezyjczycy z Francuskiego Korpusu Ekspedycyjnego, którzy musieli
      zaskoczyć strzelców spadochronowych atakiem w górach Aurunci. Wycofującym się Niemcom drogę miał przeciąć
      amerykański 6 Korpus uderzeniem z przyczółka pod Anzio. W sumie więc sprzymierzeni zgromadzili przeciwko 14
      dywizjom i trzem brygadom niemieckim 21 dywizji i 11 samodzielnych brygad. Alianci przeważali też w broni
      pancernej i artylerii, a ich lotnictwo całkowicie zdominowało przestrzeń powietrzną nad masywem Monte Cassino.
      Jednakże tę przewagę wciąż niwelowała umiejętna obrona niemieckich strzelców spadochronowych i górskich. Churchill
      miał rację, pisząc że „nieprzyjacielowi też jest bardzo ciężko”. Lecz jak wspominał jeden z oficerów elitarnej 1
      Dywizji Strzelców Spadochronowych, pułkownik Rudolf Böhmler: „Spadochroniarze wiedzieli dobrze, jaka jest stawka.
      Rozkaz utrzymania Cassino we wszelkich okolicznościach oznaczał walkę do śmierci”. Jednak tym razem do walki z
      nimi szedł równie zdeterminowany przeciwnik: żadna z nacji walczących pod Monte Cassino nie miała z Niemcami
      takich rachunków do wyrównania, jak Polacy.
    </p>
    <h4 class="fw-bold">Krew spływa z gór</h4>
    <p>
      Prawie cały kwiecień trwało przerzucanie i instalowanie na pozycjach jednostek wyznaczonych do czwartego natarcia
      na masyw Monte Cassino. Ta gigantyczna operacja była trudna nie tylko z powodów logistycznych czy terenowych. Jak
      zapisał generał Anders: „Warunki, w jakich znajdowały się oddziały na stanowiskach obronnych i wyjściowych do
      natarcia, były bardzo trudne. Cały przyfrontowy teren był dniem i nocą pod ciężkim nękającym ogniem artylerii i
      moździerzy nieprzyjaciela. Żołnierz przebywał w ukryciu z prymitywnych składaków lub dużych kamieni. O wkopaniu
      się w skały nie było mowy”.
      27 kwietnia generał Anders zaprosił dowódców dwóch głównych jednostek swego korpusu, by przez losowanie wybrali
      kierunki, w jakich mieli uderzyć na Niemców. Generał Bronisław Duch dla swojej 3 Dywizji Strzelców Karpackich
      wyciągnął atak na wzgórza 593–569, Massa Albanettę i następnie na klasztor. 5 Kresowa Dywizja Piechoty generała
      Nikodema Sulika opanować miała Widmo, San Angello oraz wzgórza 575 i ruszyć na linię Hitlera. Dzień „D”, czyli
      natarcia, ustalono na 11 maja.
      Po kilkunastu upalnych dniach, 11 maja spadł nieduży deszcz. Oficerowie odbyli ostatnie odprawy, sprawdzono
      ostatnie szczegóły. W oddziałach odczytano rozkazy głównodowodzących. Generał Anders swój rozkaz do żołnierzy
      zakończył słowami: „Z wiarą w sprawiedliwość Opatrzności Boskiej idziemy naprzód ze świętym hasłem w sercach
      naszych: Bóg, Honor i Ojczyzna”.. Natomiast dowódca 8 Armii, generał Leese, swój rozkaz zakończył słowami admirała
      Nelsona spod Trafalgar: „Niech każdy wykona swój obowiązek”. Wieczorem wszyscy ruszyli wykonywać swój obowiązek:
      Anglicy i Hindusi z 13 Korpusu nad Rapido, marokańscy i tunezyjscy górale w górach Aurunci, a Amerykanie na
      wybrzeżu tyrreńskim. Wreszcie do natarcia ruszyły polskie bataliony karpackie i kresowe.
      Już w pierwszych chwilach ataku okazało się, że nawała artyleryjska, która go poprzedziła, niewiele zaszkodziła
      nieprzyjacielowi. Spadochroniarze otworzyli do atakujących morderczy ogień ze swych bunkrów, doskonale
      wkomponowanych w teren, zamaskowanych głazami i krzakami. Można je było zlokalizować dopiero wtedy, kiedy zaczęły
      ziać seriami cekaemów i erkaemów. Generał Anders podkreślał w swych wspomnieniach: „Ciemności całkowite nocy i
      dymu. Na kilka kroków nic nie widać. Nawet żołnierze tej samej drużyny w pochodzie lub próbie pochodu naprzód,
      padając w ogniu i zrywając się znowu, po wybuchach bliskich lub pośród nich, tracą łączność, z biedą się
      odnajdują, nie doliczają się swego stanu. Od samego początku padają, zabici lub ranni, dowódcy baonów, kompanii,
      plutonów, a dowództwo obejmują kolejno zastępcy i zastępcy zastępców”.
      Mimo tego bataliony 1 Brygady Strzelców Karpackich zdołały wedrzeć się na wzgórze 593 i podejść do tzw. Gardzieli,
      prowadzącej na Massę Albanettę i rozpocząć walkę o wzgórze 569. Natomiast 5 Wileńska Brygada Piechoty wdarła się
      na wzgórze, nazwane przez Amerykanów w czasie ich pierwszego natarcia Widmem. To było prawdziwe „kretowisko”
      bunkrów, które polscy strzelcy musieli zdobywać jeden po drugim, odnosząc przy tym ogromne straty. Kresowiacy
      starali się przedrzeć w kierunku wzgórza 575, ale nie uszli daleko, gdyż niemiecki ogień dokładnie pokrywał całą
      dolinę. Jeszcze gorzej wiodło się żołnierzom atakującym San Angelo. Zdziesiątkowane kompanie wycofały się na
      wzgórze 706.
      Szybko okazało się, że łatwiej jest zająć nieprzyjacielskie pozycje, niż je utrzymać. Nacierający i obrońcy zwarli
      się w zażartej walce, która osiągnęła niespotykane wcześniej nawet w tej bitwie natężenie. Pułkownik dyplomowany
      Ludwik Domoń pisał w swym raporcie: „Zaciętość jest tak duża, że ten, kto tych warunków nie widział, nie jest w
      stanie [ich] zrozumieć. Dla przykładu melduję, że plutonowy Gorgolewski z 18 L[wowskiego] B[atalionu] S[trzelców],
      przecięty nieprzyjacielskim cekaemem najpierw przez nogi strzela do bunkra dalej, a później postrzelony drugi raz
      przez piersi unosi się i krzyczy: «Koledzy, a bijcie tych skurwysynów» i wtedy dopiero od strzału strzelca
      wyborowego pada trafiony w głowę z okrzykiem zamierającym na ustach: «Niech żyje Polska»”.
      Niestety, ofiarność i szaleńcza odwaga Polaków nie zdołała tym razem pokonać niemieckich spadochroniarzy. Na nic
      się zdało wysłanie Shermanów na Gardziel – zostały zdziesiątkowane na minach i ogniem dział przeciwpancernych. Jak
      podkreśla generał Anders, „Natychmiastowe wprowadzenie do natarcia świeżych sił było niemożliwe ze względu na
      bardzo małą przepustowość dróg i ścieżek dofrontowych oraz brak miejsca na małym zapleczu frontu”. Trzeba było
      odtrąbić odwrót, ale to pierwsze, nieudane natarcie nie poszło całkowicie na marne. Dzięki temu, że Niemcy musieli
      maksymalnie zaangażować się w walkę z polskimi żołnierzami, Brytyjczycy z 13 Korpusu zdołali zdobyć przyczółki
      mostowe i przełamać czołowe stanowiska linii „Gustawa”. Pułkownik Böhmler, który dowodził obroną wzgórza 593,
      napisał z podziwem w raporcie: „Strzelcy karpaccy prowadzili wspaniałą walkę…”. Natarcie i bój z 11–14 maja
      kosztowały Polaków 205 poległych, 380 zaginionych i 1000 rannych. Liczba zaginionych z każdym następnym dniem
      malała, gdyż żołnierze powracali z obszary niedawnych walk, a nawet wyrywali się z niewoli niemieckiej.
    </p>
    <h4 class="fw-bold">Biało-czerwona obwieszcza Victorię</h4>
    <p>
      Tymczasem w sztabie 2 Korpusu generał Anders ze swymi oficerami szykował już kolejne natarcie. Wyznaczono je na
      godzinę 7.00 17 maja. Zasadniczy plan i kierunek natarcia pozostał bez zmian. Polskie bataliony ruszyły do ataku
      tuż za nawałnicą ogniową własnej artylerii. Polacy mieli o tyle łatwiej, że przeskakiwali już rozpoznane podczas
      pierwszego natarcia nieprzyjacielskie zapory ogniowe, co nie znaczy, że nie ponosili strat. Żołnierze ginęli nie
      tylko od ognia wroga, ale i min i pułapek. Jednak karpatczycy i kresowiacy byli już nie do powstrzymania. Tego
      dnia zdobyli grzbiet Widma (za wyjątkiem jego północnej części) oraz wzgórze 593 i Gardziel.
      Znowuż przeciwnicy zwarli się w morderczej walce i wydawało się, że może się powtórzyć scenariusz z pierwszego
      natarcia. Lecz następnego dnia generał Anders rzucił do walki bataliony, które skrwawiły się 11 maja, wzmocnione
      kompanią komandosów i częścią 15 Pułku Ułanów Poznańskich. W końcu wysłał też dwa bataliony zebrane z
      artylerzystów, kierowców, zaopatrzeniowców, kucharzy i innych żołnierzy tyłowych. Generał wspominał: „Była to
      chwila kryzysu bitwy, kiedy obie strony całkowicie wyczerpane leżą naprzeciw siebie, zdawałoby się, niezdolne do
      wysiłku. Zwycięża wtedy silniejszy wolą, ten co zdobędzie się na ostateczne uderzenie…”.
      Rankiem 18 maja Polacy poderwali się do boju. Około 6.00 żołnierze 6 batalionu strzelców karpackich wdarli się do
      Masseria Albaneta. W tym samym czasie na wzgórze 569 wchodzili żołnierze 5 batalionu, przełamując obronę
      niemieckich oddziałów osłonowych, które pozostały po tym, jak w nocy wycofało się większość ich sił. O 9.00 w
      kierunku Góry Klasztornej ruszyły patrole 12 Pułku Ułanów Podolskich, wyciągając z bunkrów nielicznych
      spadochroniarzy, którzy nie zdążyli się wycofać lub jako sanitariusze pozostali przy rannych. Oni też jako pierwsi
      objęli we władanie ruiny klasztorne, na których powiesili proporzec w barwach 12 Pułku – amarantowo-granatowych.
      Około 11.30 żołnierze z trzeciego plutonu 3 kompanii 5 batalionu 3 Dywizji Strzelców Karpackich pod dowództwem
      podporucznika Adama Lorenza umieścili w klasztorze biało-czerwoną flagę, „aby wszyscy wiedzieli i z daleka
      widzieli, kto zdobył te gruzy na szczycie”.
      Niebawem do sztabu generała Andersa z gratulacjami przybył generał Leese. Za jego zgodą, generał Anders polecił
      obok polskiego sztandaru umieścić brytyjski Union Jack, na znak braterstwa broni z żołnierzami 13 Korpusu i na
      znak przynależności 2 Korpusu do 8 Armii. W tym czasie nadal trwały zacięte walki na odcinku 5 Kresowej Dywizji
      Piechoty. Dopiero następnego dnia, 19 maja, zdołano oczyścić z nieprzyjaciela wzgórze 575. Niemcy wycofali się
      ostatecznie na linię Hitlera.
      Zewsząd do sztabu 2 Korpusu płynęły gratulacje. Gazety podkreślały bojowe walory polskich żołnierzy. Wydawało się
      przez chwilę, że sprawa polska nie będzie mogła już być porzucona przez zachodnich aliantów. Próżne to były
      nadzieje. 2 Korpus ruszył do dalszej, równie trudnej i krwawej kampanii, a Amerykanie i Brytyjczycy do swej
      polityki ustępstw wobec Stalina. Na otarcie łez pozostały polskim żołnierzom słowa uznania generała Alexandra:
      „Jeżeliby mi dano do wyboru pomiędzy jakimikolwiek żołnierzami, których chciałbym mieć pod swoim dowództwem,
      wybrałby was, Polaków”. Bitwa pod Monte Cassino kosztowała 2 Korpus Polski 924 poległych, 2930 rannych i 345
      zaginionych. Na miejscu wiecznego spoczynku u stup wzgórza, które przyszło im zdobywać, wyryto napis: „Przechodniu
      powiedz Polsce, żeśmy polegli wierni w jej służbie”.
    </p>
